import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import download   from  '../components/download.vue'
import domainList   from  '../components/domainList.vue'
import Home   from  '../components/Home.vue'
import domainInteraction  from  '../components/domainInteraction.vue'
import NeuralStyle   from  '../components/NeuralStyle.vue'
import Contact   from  '../components/Contact.vue'
//import pdbdetail  from  '../components/pdbfile.vue'
import Bind from '../components/bind.vue'
//import searchLists from  '../views/searchLists.vue'
//import blast from '../components/blast.vue'
import cladetail from '../components/cladetail.vue'
import { component } from 'vue/types/umd'
const searchLists = () => import('../views/searchLists.vue')
const pdbdetail = () => import('../components/pdbfile.vue')
const standardclass = () => import('../components/standardclass.vue')
const tutorial = () => import('../components/tutorial.vue')
Vue.use(VueRouter)


const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },{
    path: '/tutorial',
    name: 'tutorial',
    component: tutorial
  },{
    path: '/standardclass',
    name: 'standardclass',
    // component：BrickModel
    component: standardclass
  },{
    path: '/download',
    name: 'download',
    component: download
  },{
    path: '/domainList',
    name: 'product',
    component: domainList
  },{
    path: '/domainInteraction',
    name: 'domainInteraction',
    component: domainInteraction
  },{
    path: '/NeuralStyle',
    name: 'NeuralStyle',
    component: NeuralStyle
  },{
    path: '/Contact',
    name: 'contact',
    component: Contact
  },{
    path: '/searchLists',
    name: 'searchLists',
    component: searchLists
  },{
    path: '/ProteinBind'+'/:id',
    name: 'Bind',
    component:Bind
  },{
    path: '/:name',
    name: 'pdbId',
    //component:pdbdetail
    component: pdbdetail

  },{
    path: '/ProteinBind/:id',
    name: 'Bind',
    component:Bind
  },

]

const router = new VueRouter({
  routes,
  mode:'history'
})

export default router
