<script>
import axios from "axios";

export default {
  name: "showbind",
  data() {
    return {
      bindfile: [],
      search: "",
    };
  },
  created() {
    axios.get("./bindn.json").then((res) => {
      for (let key in res.data) {
        this.bindfile.push(res.data[key]);
      }
    });
  },
  computed: {
    filteredpdbs: function() {
      return this.bindfile.filter((bind) => {
        return bind.Id.match(this.search);
      });
    },
  },
};
</script>

